import card from '@/components/good-card/card.vue'
	export default {
		components:{uniNumberBox,card},
		data() {
			return {
				showCard:false,
				editData:{
					specList:[],
					goodsType:[],
					spec:'',
					type:''
				},
				showType:'',//控制显示弹窗
				sumPrice:'0.00',
				headerPosition:"fixed",
				headerTop:null,
				statusTop:null,
				selectedList:[],
				isAllselected:false,
				goodsList:[],
				//控制滑动效果
				theIndex:null,
				oldIndex:null,
				isStop:false
			}
		},
		onPageScroll(e){
			//兼容iOS端下拉时顶部漂移
			this.headerPosition = e.scrollTop>=0?"fixed":"absolute";
			this.headerTop = e.scrollTop>=0?null:0;
			this.statusTop = e.scrollTop>=0?null:-this.statusHeight+'px';
		},
		//下拉刷新，需要自己在page.json文件中配置开启页面下拉刷新 "enablePullDownRefresh": true
		onPullDownRefresh() {
		    setTimeout(function () {
				console.log('下拉再次请求购物车');
				uni.getStorage({
					key:'cartList',
					success: (res) => {
						this.goodsList = res.data;
					},
					fail: (err) => {
						this.goodsList = [];
					}
				});
		        uni.stopPullDownRefresh();
		    }, 1000);
		},
		onLoad() {
			//兼容H5下结算条位置






			
			// uni.setStorage({
			// 	key:'cartList',
			// 	data:[
			// 		{
			// 			id:10,
			// 			img:'../../static/img/goods/p1.jpg',
			// 			name:'蝴蝶结丝绒长袖吊带连衣裙欧美复古',
			// 			type:2,
			// 			spec:1,
			// 			specList:["XS","S","M","L","XL","XXL"],
			// 			goodsType:["粉紫","樱桃红","湖水蓝"],
			// 			price:127.5,
			// 			number:1,
			// 			// selected:false,
			// 		},
			// 		{
			// 			id:22,
			// 			img:'../../static/img/goods/p2.jpg',
			// 			name:'森系少女复古手链手环网红Ins',
			// 			type:4,
			// 			spec:1,
			// 			specList:["XS","S","M","L","XL","XXL"],
			// 			goodsType:["玫瑰金色链子粉晶拼珍珠","彩虹珠子草编森女手串","链子","特价散珠*1","波罗的海银色手镯"],
			// 			price:127.5,
			// 			number:1,
			// 			// selected:false,
			// 		},
			// 		{
			// 			id:33,
			// 			img:'../../static/img/goods/p3.jpg',
			// 			name:'阳澄湖精品大闸蟹',
			// 			type:0,
			// 			spec:1,
			// 			specList:["500g","250g","1kg"],
			// 			goodsType:["公蟹",'母蟹','公母各半'],
			// 			price:127.5,
			// 			number:1,
			// 			// selected:false,
			// 		},
			// 	]
			// });
		},
		onShow() {
			uni.getStorage({
				key:'cartList',
				success: (res) => {
					this.goodsList = res.data;//selectList
				},
				fail: (err) => {
					this.goodsList = [];
				}
			});
		},
		watch:{
			goodsList:{
				handler(newV,oldV){
					this.sum();
					uni.setStorage({
						key:'cartList',
						data:newV
					});
				},
				deep:true
			}
		},
		methods: {
			updateData(data,type){
				if(type==3){
					this.goodsList.forEach((item,index) =>{
						if(item.id == data.id){
							this.goodsList.splice(index,1,data);
						}
					});
				}
				
			},
			toShowType(row){
				this.showCard = true;
				this.editData = JSON.parse(JSON.stringify(row));
				
			},
			changeNumber(value,row){//改变数字
				row.number = value;
			},
			//加入商品 参数 goods:商品数据
			joinGoods(goods){
				/*
				* 这里只是展示一种添加逻辑，模板并没有做从其他页面加入商品到购物车的具体动作，
				* 在实际应用上，前端并不会直接插入记录到goodsList这一个动作，一般是更新列表和本地列表缓存。
				* 一般商城购物车的增删改动作是由后端来完成的,
				* 后端记录后返回前端更新前端缓存
				*/
				let len = this.goodsList.length;
				let isFind = false;//是否找到ID一样的商品
				for(let i=0;i<len;i++){
					let row = this.goodsList[i];
					if(row.id==goods.id )
					{	//找到商品一样的商品
						this.goodsList[i].number++;//数量自增
						isFind = true;//找到一样的商品
						break;//跳出循环
					}
				}
				if(!isFind){
					//没有找到一样的商品，新增一行到购物车商品列表头部
					this.goodsList[i].unshift(goods);
				}
			},

			//商品跳转
			toGoods(e){
				uni.showToast({title: '商品'+e.id,icon:"none"});
				uni.navigateTo({
					url: '../../goodPage/goods/goods' 
				});
			},
			//跳转确认订单页面
			toConfirmation(){
				let tmpList=[];
				let len = this.goodsList.length;
				for(let i=0;i<len;i++){
					if(this.goodsList[i].selected) {
						tmpList.push(this.goodsList[i]);
					}
				}
				if(tmpList.length<1){
					uni.showToast({
						title:'请选择商品结算',
						icon:'none'
					});
					return ;
				}
				uni.setStorage({
					key:'buylist',
					data:tmpList,
					success: () => {
						uni.navigateTo({
							url:'../../pages/order/confirmation'
						})
					}
				});
			},
			//删除商品
			deleteGoods(id){
				let len = this.goodsList.length;
				for(let i=0;i<len;i++){
					if(id==this.goodsList[i].id){
						this.goodsList.splice(i, 1);
						break;
					}
				}
				this.selectedList.splice(this.selectedList.indexOf(id), 1);
				this.sum();
				this.oldIndex = null;
				this.theIndex = null;
			},
			// 删除商品s
			deleteList(){
				let len = this.selectedList.length;
				while (this.selectedList.length>0)
				{
					this.deleteGoods(this.selectedList[0]);
				}
				this.selectedList = [];
				this.isAllselected = this.selectedList.length == this.goodsList.length && this.goodsList.length>0;
				this.sum();
			},
			// 选中商品
			selected(index){
				this.goodsList[index].selected = this.goodsList[index].selected?false:true;
				let i = this.selectedList.indexOf(this.goodsList[index].id);
				i>-1?this.selectedList.splice(i, 1):this.selectedList.push(this.goodsList[index].id);
				this.isAllselected = this.selectedList.length == this.goodsList.length;
				this.sum();
			},
			//全选
			allSelect(){
				let len = this.goodsList.length;
				let arr = [];
				for(let i=0;i<len;i++){
					this.goodsList[i].selected = this.isAllselected? false : true;
					arr.push(this.goodsList[i].id);
				}
				this.selectedList = this.isAllselected?[]:arr;
				this.isAllselected = this.isAllselected||this.goodsList.length==0?false : true;
				this.sum();
			},
			// 合计
			sum(e,index){
				this.sumPrice = 0;
				let len = this.goodsList.length;
				for(let i=0;i<len;i++){
					if(this.goodsList[i].selected) {
						if(e && i==index){
							this.sumPrice = this.sumPrice + (e.detail.value*this.goodsList[i].price);
						}else{
							this.sumPrice = this.sumPrice + (this.goodsList[i].number*this.goodsList[i].price);
						}
					}
				}
				this.sumPrice = this.sumPrice.toFixed(2);
			},
			discard() {
				//丢弃
			}
			
			
		}
	}
